// @ts-nocheck
import ERC20_INTERFACE from '../constants/abis/erc20'
import { useMultipleContractSingleData } from '../state/multicall/hooks'
import {utils} from 'ethers'
import airdropRecipients from './airdropRecipients'
import {MerkleTree} from 'merkletreejs'
import keccak256 from 'keccak256'
import {useContract} from '../hooks/useContract'
import {useState, useEffect} from 'react'
import {plebTokenAddress} from './settings'

const elements = []
for (const recipientAddress in airdropRecipients) {
  const recipientAmount = airdropRecipients[recipientAddress] + '000000000000000000'
  elements.push(utils.solidityPack(['address', 'uint256'], [recipientAddress, recipientAmount]))
}
const merkleTree = new MerkleTree(elements, keccak256, { hashLeaves: true, sortPairs: true })

export function usePlebBalance(address: string): string {
  address = address && address.toLowerCase()
  const balances = useMultipleContractSingleData(
    [plebTokenAddress], ERC20_INTERFACE, 'balanceOf', [address]
  )
  let balance = '-'
  try {
    balance = utils.formatUnits(balances[0].result.balance)
    const parts = balance.split('.')
    if (parts[1].length > 3) {
      parts[1] = parts[1].slice(0, 3)
    }
    if (parts[1].length === 0) {
      parts[1] = '0'
    }
    balance = parts.join('.')
  }
  catch (e) {
    // console.log(e)
  }
  return balance
}

export function usePlebAirdropBalance(address: string): string {
  address = address && address.toLowerCase()
  let balance = airdropRecipients[address]
  if (!balance) {
    balance = '0.0'
  }
  else {
    balance += '.0'
  }
  return balance
}

const airdropAbi = [{
  "inputs": [
    {
      "internalType": "uint256",
      "name": "amount",
      "type": "uint256"
    },
    {
      "internalType": "bytes32[]",
      "name": "merkleProof",
      "type": "bytes32[]"
    }
  ],
  "name": "claimAirdrop",
  "outputs": [],
  "stateMutability": "nonpayable",
  "type": "function"
},{
  "inputs": [
    {
      "internalType": "address",
      "name": "recipient",
      "type": "address"
    },
    {
      "internalType": "uint256",
      "name": "amount",
      "type": "uint256"
    },
    {
      "internalType": "bytes32[]",
      "name": "merkleProof",
      "type": "bytes32[]"
    }
  ],
  "name": "airdropIsClaimed",
  "outputs": [
    {
      "internalType": "bool",
      "name": "",
      "type": "bool"
    }
  ],
  "stateMutability": "view",
  "type": "function"
}]

// TODO: test to see if it works with real contract address
export function useAirdropIsClaimed(address: string): boolean | undefined {
  address = address && address.toLowerCase()
  // user has no airdrop
  const addressHasAirdropDefaultState = !airdropRecipients[address] ? false : undefined
  const [airdropIsClaimed, setAirdropIsClaimed] = useState(addressHasAirdropDefaultState)
  const contract = useContract(plebTokenAddress, airdropAbi)

  useEffect(() => {
    if (contract && addressHasAirdropDefaultState === undefined && airdropIsClaimed !== true) {
      const amount = airdropRecipients[address] + '000000000000000000'
      const leaf = keccak256(utils.solidityPack(['address', 'uint256'], [address, amount]))
      const proof = merkleTree.getHexProof(leaf)

      contract.airdropIsClaimed(address, amount, proof).then(_airdropIsClaimed => {
        // console.log('contract.airdropIsClaimed response:', _airdropIsClaimed)
        if (_airdropIsClaimed !== airdropIsClaimed) {
          setAirdropIsClaimed(_airdropIsClaimed)
        }
      })
    }
  }, [contract, addressHasAirdropDefaultState, airdropIsClaimed, address])

  return airdropIsClaimed
}

export function useClaimAirdrop() {
  const contract = useContract(plebTokenAddress, airdropAbi)

  const claimAirdrop = async (address) => {
    address = address && address.toLowerCase()
    // const root = merkleTree.getHexRoot()
    const amount = airdropRecipients[address] + '000000000000000000'
    const leaf = keccak256(utils.solidityPack(['address', 'uint256'], [address, amount]))
    const proof = merkleTree.getHexProof(leaf)

    console.log('claimAirdrop(', amount, ',', proof, ')')
    console.log({elements, leaf, proof})
    try {
      await contract.claimAirdrop(amount, proof)
    }
    catch (e) {
      console.log(e)
      window.alert(e.data.message)
    }
  }
  return claimAirdrop
}
